.cover {
  height: 100vh;
  width: 100vw;
  max-width: 100%;
  background-image: url(../assets/img/cover.jpg);
  background-size: cover;
  background-position: center; }
  .cover__overlay {
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    position: relative;
    align-content: center;
    justify-content: center;
    flex-direction: column; }
  .cover__link {
    position: absolute;
    bottom: 2rem;
    color: #fff;
    left: calc(50vw - 2rem); }
  .cover__button {
    width: 4rem;
    height: 4rem; }

.header {
  max-width: 1125px;
  margin: auto; }
  .header__title {
    color: #fff;
    margin: 0;
    padding: .5rem;
    font-size: 4rem;
    border: 4px #fff solid; }
  .header__subtitle {
    margin: 0.5rem 0;
    color: #fff;
    font-size: 2.5rem;
    text-align: center; }

.about {
  margin: auto;
  padding: 4rem 1rem;
  background-color: white; }
  .about__container {
    display: flex;
    flex-direction: row; }
    @media screen and (max-width: 768px), (max-width: 320px) {
      .about__container {
        flex-direction: column; } }
  .about__content {
    margin-left: 5rem;
    width: 50%; }
    @media screen and (max-width: 768px), (max-width: 320px) {
      .about__content {
        width: 100%;
        margin: .5rem .5rem .5rem .5rem; } }
  .about__description {
    font-size: 1.25rem;
    line-height: 1.75rem; }
  .about__photo {
    overflow: hidden;
    width: 200px;
    height: 200px;
    flex: 0 0 200px;
    align-self: center;
    justify-self: center; }
    .about__photo--circle {
      border-radius: 50%; }
    .about__photo img {
      width: 100%; }
  .about__title {
    font-size: 2.5rem; }

.experience {
  margin: auto;
  padding: 4rem 1rem;
  background: #41A0F2;
  color: #fff; }
  @media screen and (max-width: 320px) {
    .experience {
      padding: 4rem 0; } }
  .experience__container {
    padding: 0 1rem; }
  .experience__title {
    color: #fff;
    text-align: center;
    font-size: 2.5rem;
    margin: 0 0 4rem 0; }

.timeline {
  max-width: 900px;
  margin: 0 auto;
  position: relative; }
  .timeline::before {
    content: '';
    position: absolute;
    width: 4px;
    height: 90%;
    background: #e4e4e4;
    left: 30px;
    top: 20px;
    margin-left: -2px;
    box-shadow: 0 0 8px #333; }
  @media screen and (max-width: 320px) {
    .timeline::before {
      content: none; } }
  .timeline__date {
    margin: 0;
    margin-bottom: .5rem;
    font-size: 1rem;
    font-weight: 500;
    color: #585858; }
  .timeline__description {
    line-height: 1.5rem;
    margin: 0;
    margin-bottom: .5rem;
    font-size: 1rem;
    font-weight: 300;
    text-align: justify;
    color: #2e2e2e; }
    @media screen and (max-width: 320px) {
      .timeline__description {
        font-weight: 400; } }
  .timeline__image {
    position: absolute;
    background: #fff;
    width: 70px;
    height: 70px;
    left: -105px;
    top: 15px;
    border-radius: 50%;
    text-align: center;
    border: 4px solid #e4e4e4;
    box-shadow: 0 0 8px #333; }
    @media screen and (max-width: 320px) {
      .timeline__image {
        left: calc(50% - 70px / 2);
        top: -35px; } }
    .timeline__image img {
      width: 95%;
      height: 95%;
      object-fit: contain; }
  .timeline__item {
    color: #000;
    box-shadow: 0 0 16px #333;
    background: white;
    padding: 1rem 3rem;
    border-radius: 4px;
    margin-bottom: 2rem;
    margin-left: 100px;
    position: relative; }
    @media screen and (max-width: 320px) {
      .timeline__item {
        margin-left: 0;
        margin-bottom: 4rem;
        padding: 3rem 1rem 1rem 1rem; } }
  .timeline__title {
    margin: 0;
    margin-bottom: .5rem;
    font-size: 1.5rem;
    font-weight: 500;
    color: #3b3b3b; }

.projects {
  padding: 4rem 1rem; }
  .projects__description {
    font-size: 1.25rem; }
  .projects__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: minmax(200px, auto);
    column-gap: 8px; }
    @media screen and (max-width: 768px) {
      .projects__grid {
        grid-template-columns: repeat(1, 1fr); } }
  .projects__title {
    color: #000;
    text-align: center;
    font-size: 2.5rem;
    margin: 0 0 4rem 0; }

.project {
  background: #212528;
  color: #a3a3a3;
  border-radius: 4px;
  overflow: hidden;
  margin: 1rem 0;
  line-height: 1.5rem;
  font-size: 1rem;
  font-weight: 400;
  font-family: Roboto Mono;
  box-shadow: 0 4px 16px #333; }
  @media screen and (max-width: 768px) {
    .project {
      line-height: 1.25rem;
      font-size: 0.75rem; } }
  .project--light {
    background: #ffffff;
    color: #8d8d8d; }
  .project__circle {
    display: block;
    width: 10px;
    height: 10px;
    background: #fff;
    border-radius: 50%;
    margin-right: 5px; }
    .project__circle--red {
      background: #ff0000; }
    .project__circle--green {
      background: #00da00; }
    .project__circle--yellow {
      background: #ffd015; }
  .project__content {
    padding: 1.25rem 3rem 3.5rem; }
    @media screen and (max-width: 768px) {
      .project__content {
        padding: 1.25rem 1rem 1.5rem; } }
  .project__grid {
    display: grid;
    column-gap: 0.5rem;
    margin: 0;
    grid-template-columns: 116px 1fr; }
    @media screen and (max-width: 768px) {
      .project__grid {
        grid-template-columns: 1fr; } }
    .project__grid--description {
      margin-bottom: 1.5rem; }
  .project__header {
    background: #1e1e1e;
    color: #cacaca;
    height: 40px;
    padding-left: 1rem;
    padding-right: 1rem;
    display: flex;
    align-items: center; }
    .project__header--light {
      background: #b6b6b6;
      color: #4e4e4e; }
  .project__label {
    color: #808082;
    text-align: right;
    display: block;
    font-weight: normal; }
    @media screen and (max-width: 768px) {
      .project__label {
        text-align: left; } }
    .project__label--light {
      color: #808082; }
  .project__link {
    color: #14b5d0; }
  .project__name {
    color: #fff; }
    .project__name--light {
      color: #000; }
  .project__titlebar {
    width: 100%;
    font-weight: bold;
    text-align: center; }

.contact {
  background: #1D79F2;
  color: #fff;
  padding: 4rem 1rem 2rem;
  margin: auto; }
  .contact__container {
    margin-bottom: 4rem; }
  .contact__description {
    font-size: 1.25rem;
    margin-bottom: 28px; }
  .contact__mail {
    text-decoration: none;
    font-style: normal;
    font-weight: normal;
    font-size: 1.25rem; }
    .contact__mail--link {
      color: #fff;
      font-weight: 700;
      padding-bottom: 8px;
      border-bottom: solid 2px #fff;
      text-decoration: none; }
  .contact__title {
    text-align: left;
    font-size: 2.5rem;
    margin: 0 0 2rem 0; }

.footer__text {
  text-align: center;
  margin: 0 auto 0; }

.social {
  list-style: none;
  display: flex;
  padding: 0;
  margin-bottom: 24px; }
  .social__link {
    text-decoration: none; }
  .social__icon {
    margin: 0 8px 0 0;
    width: 50px;
    height: 50px;
    transition: all .2s ease-in-out; }
    .social__icon:hover {
      transform: scale(1.5); }
    .social__icon--linkedin {
      content: url(../assets/img/socials/LinkedIn-social.png); }
    .social__icon--github {
      content: url(../assets/img/socials/GitHub-social.png); }
    .social__icon--youtube {
      content: url(../assets/img/socials/YouTube-social.png); }

html {
  box-sizing: border-box;
  font-size: 16px;
  font-family: 'Roboto', sans-serif;
  scroll-behavior: smooth; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  color: #000;
  background: #fff;
  width: 100%;
  height: 100%; }

.container {
  margin: auto;
  max-width: 1125px; }

.link {
  color: #14b5d0;
  text-decoration: none;
  font-weight: 600; }
